
<template>
  <div class="search row">
    <div class="col-md-6">
      <fg-select
        name="searchType"
        :options="selectOptions"
        @changeValue="handleSelect"
        v-if="selectable"
      />
    </div>
    <div class="col-md-6">
      <fg-input
        @changeValue="handleSearch"
        name="search"
        :placeholder="placeholder"
      />
    </div>
  </div>
</template>

<script>
export default {
  name: 'searchbox',
  props: {
    placeholder: {
      default: 'search',
      type: String
    },
    selectable: {
      default: false,
      type: Boolean
    },
    selectOptions: {
      default: () => [],
      type: Array
    },
    selected: {
      default: '',
      type: String
    }
  },
  methods: {
    handleSearch(value) {
      this.$emit('handleSearch', value)
    },
    handleSelect(value, key) {
      this.$emit('handleSelect', { value, key })
    }
  }
}
</script>



